.collection {
    display: grid;
    grid-template-areas: "a b";
    grid-template-columns: 250px 1fr;
    background-color: whitesmoke;

}

.navMenu {
    grid-area: a;
    margin: 10px 5px 10px 0px;
    font-size: 30px;
}

.navMenu a {
    text-decoration: none;
}

.dressContent {
    grid-area: b;
}

.ithem {
    font-weight: 600;
    margin: 45px 0px 20px 10px;
}

.ithem a {
    color: black;
}

.ithem a.activeLink {
    color: firebrick;
}


