.app-wrapper{
    display: grid;
    max-width: 1200px;
    margin: 0 auto;

    grid-template-areas:
  "h"
  "c"
  "f";

    grid-template-rows: 160px 1fr 120px;

    grid-gap: 10px;

}

@media (max-width:1200px){
    .app-wrapper{
        max-width: 970px;
    }
}

.app-wrapper-content{
    grid-area: c;
}
