.header {
    grid-area: h;
    background-color: whitesmoke;
    display: flex;
    z-index: 10;

}
.header a{
    text-decoration: none;
}

.logo{
    font-family: Lobster;
    font-size: 68px;
    line-height: 1.2;
    font-weight: 600;
    letter-spacing: 0.1em;

    margin: 0px 30px 40px 50px;

}
.logo a{
    color: #605f5f;
}

.nav{
    display: flex;
    min-width: 1000px;
    justify-content: space-around;
    font-size: 38px;
    font-weight: 600;
    color: darkslategrey;
    padding: 110px 0px 0px 0px;
    font-family: Lobster;

}
.ithem a{
    color: #605f5f;
}
.ithem a.activeLink{
    color: darkturquoise;
}
.number{
    margin: -50px 0px 0px 0px;
    color: darkturquoise;
    cursor: pointer;
    font-size: 28px;
}
.contacts{
cursor: pointer;
}

